import template from 'lodash/template';
import fs from 'fs-extra';

async function envsub(templateFile: string, outputFile: string, envs: Record<string, string | number>): Promise<void> {
  const templateString = await fs.readFile(templateFile, 'utf8');
  const compiledTemplate = template(templateString, {
    // mustache
    interpolate: /{{([\s\S]+?)}}/g,
  });
  const outputFileContents = compiledTemplate(envs);
  await fs.writeFile(outputFile, outputFileContents);
}

export default envsub;
